:root {
  --main-background-color: #444444;
  --secondary-background-color: #555555;
  --text-color: #efefef;
  --toolbar-color: #1976D2;
}

#background {
  background-color: var(--main-background-color);
}

h1.header {
  color: var(--text-color);
  text-align: center;
  padding-top: 40px;
  text-shadow: #999999 1px 1px 1px;
  font-size: 5vw;
  font-family: Arial, Helvetica, sans-serif;

  -webkit-animation: slideFromLeft 0.75s ease-out;

          animation: slideFromLeft 0.75s ease-out;
}

p {
  color: #ffffff;
  text-align: left;
  padding-left: 5%;
  padding-right: 5%;
  font-size: 2.5vw;
  font-family: Arial, Helvetica, sans-serif;
}

div.horizontalContent{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-left: 2.5vw;
  margin-right: 2.5vw;
  margin-top: 1.5vw;
  margin-bottom: 1.5vw;

  -webkit-animation: slideFromBottom 0.75s ease-in-out;

          animation: slideFromBottom 0.75s ease-in-out;
}

div.verticalContent{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

div.infoBox{
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: rgba(0, 0, 0, 0.5);
  width: 60vw;
  border-radius: 4px;
  margin: auto;
}

div.infoBoxWide{
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: rgba(0, 0, 0, 0.5);
  width: 92.5vw;
  border-radius: 4px;
  margin: auto;
}

div.picture{
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: var(--secondary-background-color);
  border-radius: 4px;
  width: 29vw;
  margin: auto;
}

div.picture img{
  width: 29vw;
  border-radius: 4px;
}

#toolbar{
  border: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background: var(--toolbar-color);
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
  top: 0px;
  left: 0px;
  z-index: 5;
  width: 100vw;
  height: 30px;
  position: fixed;
}

.toolbar-item {
  cursor: pointer;
  color: var(--text-color);
  text-align: justify;
  text-shadow: 1px 1px 1px gray;
  padding-top: 3px;
  padding-left: 3px;
  font-size: 20px;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
}

/*Animations*/
@-webkit-keyframes slideFromBottom{
  0%   {
      -webkit-transform: translate3d(0px, 25vw, 0px);
              transform: translate3d(0px, 25vw, 0px);
      opacity: 0;
  }

  50%    {
      opacity: .25;
  }

  100% {
      -webkit-transform: translate3d(0px, 0vw, 0px);
              transform: translate3d(0px, 0vw, 0px);
      opacity: 1.0;
  }
}
@keyframes slideFromBottom{
  0%   {
      -webkit-transform: translate3d(0px, 25vw, 0px);
              transform: translate3d(0px, 25vw, 0px);
      opacity: 0;
  }

  50%    {
      opacity: .25;
  }

  100% {
      -webkit-transform: translate3d(0px, 0vw, 0px);
              transform: translate3d(0px, 0vw, 0px);
      opacity: 1.0;
  }
}

@-webkit-keyframes fadeIn{
  0%{
      opacity: 0.0;
  }

  100%{
      opacity: 1.0;
  }
}

@keyframes fadeIn{
  0%{
      opacity: 0.0;
  }

  100%{
      opacity: 1.0;
  }
}

@-webkit-keyframes slideFromLeft {
  0% {
      -webkit-transform: translate3d(-25vw, 0px, 0px);
              transform: translate3d(-25vw, 0px, 0px);
      opacity: 0;
  }
  50% {
      opacity: .25;
  }
  100% {
      -webkit-transform: translate3d(0px, 00px, 0px);
              transform: translate3d(0px, 00px, 0px);
      opacity: 1.0;
  }
}

@keyframes slideFromLeft {
  0% {
      -webkit-transform: translate3d(-25vw, 0px, 0px);
              transform: translate3d(-25vw, 0px, 0px);
      opacity: 0;
  }
  50% {
      opacity: .25;
  }
  100% {
      -webkit-transform: translate3d(0px, 00px, 0px);
              transform: translate3d(0px, 00px, 0px);
      opacity: 1.0;
  }
}


